import _get from 'lodash.get';

export const selectOffers = state => {
  const properties = _get(state, ['sellerOffers', 'list']);
  return properties
    .map(({ Offers = [], ListPrice = 0, PropertyData = {} }) =>
      Offers.map(offer => ({
        ...offer,
        ListPrice,
        Property: { PropertyData },
      })),
    )
    .flat();
};
