// @flow
import React, { useState, Fragment } from 'react';
import type { Element } from 'react';
import { IconButton, Menu, MenuItem, Typography } from '@material-ui/core';
import _get from 'lodash/get';
import { FORM_TYPE_IDS, OFFER_STEPS } from 'shared/Consts';
import type { Offer } from 'types/Offer';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { Link } from 'react-router-dom';

type Props = {
  offer: Offer,
};

const ActionButton = ({ offer }: Props): Element<typeof Fragment> => {
  const [anchorEl, setAnchorEl] = useState(null);
  const offerId = _get(offer, ['OfferId']);

  const removalForm = _get(offer, ['Forms']).find(
    form => _get(form, ['FormTypeId']) === FORM_TYPE_IDS.contingencyRemoval,
  );
  const formId = _get(removalForm, ['FormId']);

  const onClickOpenMenu = event => {
    setAnchorEl(event.currentTarget);
  };

  const onClickCloseMenu = () => {
    setAnchorEl(null);
  };

  const isOpen = Boolean(anchorEl);
  const redirectUrl = `/offer?offerId=${offerId}&step=${OFFER_STEPS.confirmTransfer}&formId=${formId}`;

  return (
    <>
      <IconButton color="primary" onClick={onClickOpenMenu}>
        <VisibilityIcon sx={{ fontSize: '1.3rem' }} />
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        keepMounted
        open={isOpen}
        onClose={onClickCloseMenu}
      >
        <MenuItem>
          <Link to={redirectUrl}>
            <Typography variant="h4">Remove</Typography>
          </Link>
        </MenuItem>
      </Menu>
    </>
  );
};

export default ActionButton;
